.NavbarItems {
    background: linear-gradient(90deg, rgb(110, 94, 254) 0%, rgba(73, 63, 252, 1)100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
}
.navbar-logo {
    color: white;
    justify-content: start;
    margin-left: 20px;
    cursor: pointer;
}
.nav-menu {
    display: grid;
    grid-template-columns: repeat(7, auto) ;
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
}
.nav-links {
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;

}
.nav-links:hover {
    background-color: #5fa1ee;
    border-right: 4px;
    transition: all 0.2s ease-out;}

.fas-bar {
    color: white;
}

.menu-icon {
display: none;
}