:root {
    --primary: #3efff8;
}
.btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;

}
.btn.hover {
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    background: white;
    color:cornflowerblue;
    tansition: 250ms;
}
.btn--primary {
    background-color: var(--primary);

}
.btn--outline {
    background-color: transparent;
    color: white;
    padding: 8px 20px;
    border-right: 4px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}
.btn--medium {
    padding: 8px 20px;
    border-radius: 4px;
    font-size: 18px;
    color: white;
}
.btn--large {
    pading: 12px 26px;
    border-radius: 4px;
    font-size: 20px;
    color: white;
}